@use "src/assets/styles/variables" as var;

.greetings {
  .description {
    &::after {
      position: absolute;
      content: "";
      top: 100%;
      left: 50%;
      width: 100%;
      height: 2px;
      background: var.$dark-background;
      transform: translate(-50%, 15px);
    }
  }

  .ask-for-access {
    margin-top: 30px;
  }
}
