// colors
$default: #f6f6f6;
$green: #54a300;
$default-inverted: #0a0a0a;
$grey: #a3a3a3;

// radius
$border-radius-xs: 4px;
$border-radius-s: 8px;
$border-radius-m: 12px;
$border-radius-l: 16px;
$border-radius-xl: 24px;
$border-radius-xxl: 32px;
$border-radius-circle: 50%;

// logical colors
$static: $default;
$text-main: $default-inverted;
$text-pale: $grey;
$dark-background: $default-inverted;
$accent-background: $green;
$blurred-background: rgba(#0a0a0a, 0.8);
