@use "fonts.scss";
@use "variables.scss" as var;

* {
  position: relative;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  max-width: 100vw;
  overflow-x: hidden;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 18px;
  line-height: 130%;
  color: var.$text-main;
}

h1,
h2,
h3 {
  margin-bottom: 25px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}

h2 {
  font-size: 28px;
}

h3 {
  font-size: 18px;
  font-weight: 500;
}

.marked {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
}

p {
  text-align: center;
}
