@font-face {
  font-family: 'Roboto';
  src: url('../fonts/roboto/Roboto-Light.ttf') format('ttf');
  src: local('Roboto Regular'), local('Roboto-Light'), url('../fonts/roboto/Roboto-Light.eot') format('eot'),
    url('../fonts/roboto/Roboto-Light.woff') format('woff'),
    url('../fonts/roboto/Roboto-Light.woff2') format('woff2');
  font-weight: 300;
  font-style: normal;
}

