@use "src/assets/styles/variables" as var;

.second-screen {
  margin: 80px 16px 0;

  .text {
    &::after {
      position: absolute;
      content: "";
      top: 100%;
      left: 50%;
      width: 100%;
      height: 2px;
      background: var.$dark-background;
      transform: translate(-50%, 15px);
    }
  }

  .title {
    margin: 8px 0 6px;
  }

  .stations {
    display: flex;
    margin-top: 24px;
    flex-direction: column;
    gap: 12px;
  }

  .state-manually-title {
    margin-top: 32px;
  }

  .state-manually {
    display: flex;
    height: 25vh;
    justify-content: center;
    align-items: flex-end;
  }

  .loader {
    top: 0;
  }

  .promise {
    font-size: 12px;
    text-align: center;
    text-transform: uppercase;
    color: var.$text-pale;
  }
}
