@use "src/assets/styles/variables" as var;

.button {
  display: inline-flex;
  width: 100%;
  min-height: 44px;
  padding: 16px 16px 12px;
  border: none;
  font-size: 16px;
  font-weight: 500;
  color: var.$static;
  background: var.$accent-background;
  justify-content: center;
  text-decoration: none;
  text-transform: uppercase;
  outline: none;
  cursor: pointer;
  letter-spacing: 0.64px;
  transition: 0.3s linear;

  .loader {
    font-size: 16px;
    display: inline-block;
    font-weight: 500;
    color: var.$text-main;
    text-shadow: 0 0 2px var.$static, 0 0 1px var.$static, 0 0 1px var.$static;
    letter-spacing: 2px;
  }

  .loader::after {
    content: "Loading";
    position: absolute;
    left: 0;
    top: 0;
    color: var.$static;
    width: 100%;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    animation: animloader 6s linear infinite;
  }

  &:disabled {
    cursor: auto;
  }

  &:hover:not(&:disabled) {
    color: var.$text-main;
  }
}

a.button {
  line-height: 1rem;
}

@keyframes animloader {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
