@use "src/assets/styles/variables" as var;

.modal {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background: var.$default;

  .wrapper {
    width: 100%;
    max-width: 480px;
    height: 100vh;
    padding: 16px;
    background: var.$default;
    padding: 160px 32px 80px;
    border-radius: 0;
  }
}
